import { initProps } from "./componentProps";
import { emit } from "./componentEmits";
import { PublicInstanceProxyHandlers } from "./componentPublicInstance";
import { proxyRefs } from "@vue/reactivity";
export function createComponentInstance(vnode, parent) {
  const instance = {
    type: vnode.type,
    vnode,
    props: {},
    parent,
    provides: parent ? parent.provides : {}, //   parent  provides   parent.provides 
    proxy: null,
    isMounted: false,
    attrs: {}, //  attrs 
    slots: {}, // 
    ctx: {}, // context 
    setupState: {}, //  setup 
    emit: () => {},
  };

  //  prod  ctx 
  //  dev 
  instance.ctx = {
    _: instance,
  };

  //  emit
  //  bind  instance 
  //  event 
  instance.emit = emit.bind(null, instance) as any;

  return instance;
}

export function setupComponent(instance) {
  // 1.  props
  //  vnode  props
  const { props } = instance.vnode;
  initProps(instance, props);
  // 2.  slots
  initSlots();

  //  component
  //  options 
  //  function 
  //  options 
  //  stateful 
  setupStatefulComponent(instance);
}

function initSlots() {
  // todo
  console.log("initSlots");
}

function setupStatefulComponent(instance) {
  // todo
  // 1.  proxy
  console.log(" proxy");

  // proxy  instance.ctx 
  //  instance.proxy 
  //  instance.ctx  prod  dev 
  instance.proxy = new Proxy(instance.ctx, PublicInstanceProxyHandlers);
  //  instance.type
  // const Component = {setup(),render()} ....
  const Component = instance.type;
  // 2.  setup

  //  setup  props
  const { setup } = Component;
  if (setup) {
    //  currentInstance 
    //  setup 
    setCurrentInstance(instance);

    const setupContext = createSetupContext(instance);
    const setupResult = setup && setup(instance.props, setupContext);

    setCurrentInstance(null);

    // 3.  setupResult
    handleSetupResult(instance, setupResult);
  }
}

function createSetupContext(instance) {
  console.log(" setup context");
  return {
    attrs: instance.attrs,
    slots: instance.slots,
    emit: instance.emit,
    expose: () => {}, // TODO  expose 
  };
}

function handleSetupResult(instance, setupResult) {
  // setup 
  // 1.  setupResult 
  if (typeof setupResult === "function") {
    //  function  render 
    //  render 
    // setup(){ return ()=>(h("div")) }
    instance.render = setupResult;
  } else if (typeof setupResult === "object") {
    // 
    //  setupState 
    //  @vue/reactivity  proxyRefs
    // 
    // proxyRefs  setupResult 
    //  ref 
    //  setupResult  count  ref  count  count.value
    //  Ref 
    instance.setupState = proxyRefs(setupResult);
  }

  finishComponentSetup(instance);
}

function finishComponentSetup(instance) {
  //  instance  render

  //  component options
  const Component = instance.type;

  if (!instance.render) {
    // todo
    //  compile  template
    // Component.render = compile(Component.template, {
    //     isCustomElement: instance.appContext.config.isCustomElement || NO
    //   })
    instance.render = Component.render;
  }

  // applyOptions()
}

