import { ShapeFlags } from "../shared";
import { createComponentInstance } from "./component";
import {
  hostCreateElement,
  hostSetElementText,
  hostPatchProp,
  hostInsert,
  hostRemove,
} from "../runtime-dom";
import { queueJob } from "./scheduler";
import { effect } from "@vue/reactivity";
import { setupComponent } from "./component";
import { h } from "./h";

export const render = (vnode, container) => {
  console.log(" patch");
  patch(null, vnode, container);
};

function patch(n1, n2, container = null, parentComponent = null) {
  //  n2 
  //  n2  vnode
  const { type, shapeFlag } = n2;
  switch (type) {
    case "text":
      // todo
      break;
    //  static fragment comment

    default:
      //  shapeFlag 
      if (shapeFlag & ShapeFlags.ELEMENT) {
        console.log(" element");
        processElement(n1, n2, container);
      } else if (shapeFlag & ShapeFlags.STATEFUL_COMPONENT) {
        console.log(" component");
        processComponent(n1, n2, container, parentComponent);
      }
  }
}

function processElement(n1, n2, container) {
  if (!n1) {
    mountElement(n2, container);
  } else {
    // todo
    updateElement(n1, n2, container);
  }
}

function updateElement(n1, n2, container) {
  const oldProps = (n1 && n1.props) || {};
  const newProps = n2.props || {};
  //  element
  console.log(" element");
  console.log(" vnode", n1);
  console.log(" vnode", n2);

  //  el  vnode
  const el = (n2.el = n1.el);

  //  props
  patchProps(el, oldProps, newProps);

  //  children
  patchChildren(n1, n2, el);
}

function patchProps(el, oldProps, newProps) {
  //  props 
  // 1. oldProps newProps  val 
  // 
  // : oldProps.id = 1 newProps.id = 2

  // key  oldProps   newProps 
  //  newProps 
  for (const key in newProps) {
    const prevProp = oldProps[key];
    const nextProp = newProps[key];
    if (prevProp !== nextProp) {
      // 
      //  host  key
      hostPatchProp(el, key, prevProp, nextProp);
    }
  }

  // 2. oldProps  newProps 
  //  {id:1,tId:2}   {id:1}
  //  oldProps  newProps  tId 
  //  key  newProps 
  for (const key in oldProps) {
    const prevProp = oldProps[key];
    const nextProp = null;
    if (!(key in newProps)) {
      //  oldProps 
      //  newProps 
      //  host  null
      hostPatchProp(el, key, prevProp, nextProp);
    }
  }
}

function patchChildren(n1, n2, container) {
  const { shapeFlag: prevShapeFlag, children: c1 } = n1;
  const { shapeFlag, children: c2 } = n2;

  //  n2  children  text 
  //  n1  children 
  //  text 
  if (shapeFlag & ShapeFlags.TEXT_CHILDREN) {
    if (c2 !== c1) {
      console.log(" text_children, ");
      hostSetElementText(container, c2 as string);
    }
  } else {
    //  array_children
    //  array_children 
    //  children 
    if (prevShapeFlag & ShapeFlags.ARRAY_CHILDREN) {
      if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
        patchKeyedChildren(c1, c2, container);
      }
    }
  }
}

function patchKeyedChildren(c1: any[], c2: any[], container) {
  let i = 0;
  let e1 = c1.length - 1;
  let e2 = c2.length - 1;

  const isSameVNodeType = (n1, n2) => {
    return n1.type === n2.type && n1.key === n2.key;
  };

  while (i <= e1 && i <= e2) {
    const prevChild = c1[i];
    const nextChild = c2[i];

    if (!isSameVNodeType(prevChild, nextChild)) {
      console.log(" child ()");
      console.log(`prevChild:${prevChild}`);
      console.log(`nextChild:${nextChild}`);
      break;
    }

    console.log(" child  child ()");
    patch(prevChild, nextChild, container);
    i++;
  }

  while (i <= e1 && i <= e2) {
    // 
    const prevChild = c1[e1];
    const nextChild = c2[e2];

    if (!isSameVNodeType(prevChild, nextChild)) {
      console.log(" child ()");
      console.log(`prevChild:${prevChild}`);
      console.log(`nextChild:${nextChild}`);
      break;
    }
    console.log(" child  child ()");
    patch(prevChild, nextChild, container);
    e1--;
    e2--;
  }

  if (i > e1 && i <= e2) {
    //  e2 
    //  vnode
    //  c2
    while (i <= e2) {
      console.log(` vnode: ${c2[i].key}`);
      patch(null, c2[i], container);
      i++;
    }
  } else if (i > e2 && i <= e1) {
    // 
    // 
    while (i <= e1) {
      console.log(` vnode: ${c1[i].key}`);
      hostRemove(c1[i].el);
      i++;
    }
  } else {
    // 
    // 
    // a,b,[c,d,e],f,g
    // a,b,[e,c,d],f,g

    let s1 = i;
    let s2 = i;
    const keyToNewIndexMap = new Map();
    //  key  newIndex  key  newIndex
    for (let i = s2; i <= e2; i++) {
      const nextChild = c2[i];
      keyToNewIndexMap.set(nextChild.key, i);
    }

    // 
    const toBePatched = e2 - s2 + 1;
    const newIndexToOldIndexMap = new Array(toBePatched);
    for (let index = 0; index < newIndexToOldIndexMap.length; index++) {
      //  0 
      //  0 
      //  -1 
      newIndexToOldIndexMap[index] = -1;
    }
    // 
    // 1.  -> 
    // 2. >  patch
    for (i = s1; i <= e1; i++) {
      const prevChild = c1[i];
      const newIndex = keyToNewIndexMap.get(prevChild.key);
      newIndexToOldIndexMap[newIndex] = i;

      //  nexIndex 00
      //  undefined 
      //  if(newIndex) 
      if (newIndex === undefined) {
        // key  newChildren 
        hostRemove(prevChild.el);
      } else {
        // 
        console.log("");
        patch(prevChild, c2[newIndex], container);
      }
    }

    // 
    // 1.  -> 
    // 2.  [c,d,e] -> [e,c,d]
    for (i = e2; i >= s2; i--) {
      const nextChild = c2[i];

      if (newIndexToOldIndexMap[i] === -1) {
        // 
        patch(null, c2[i], container);
      } else {
        //  i+1   null
        //  hostInsert  null 
        const anchor = i + 1 >= e2 + 1 ? null : c2[i + 1];
        hostInsert(nextChild.el, container, anchor && anchor.el);
      }
    }
  }
}

function mountElement(vnode, container) {
  const { shapeFlag, props } = vnode;
  // 1.  element
  //  api
  const el = (vnode.el = hostCreateElement(vnode.type));

  // 
  if (shapeFlag & ShapeFlags.TEXT_CHILDREN) {
    // 
    // render(){
    //     return h("div",{},"test")
    // }
    //  children  test 
    console.log(`:${vnode.children}`);
    hostSetElementText(el, vnode.children);
  } else if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
    // 
    // render(){
    // Hello  component
    //     return h("div",{},[h("p"),h(Hello)])
    // }
    //  children  patch 
    mountChildren(vnode.children, el);
  }

  //  props
  if (props) {
    for (const key in props) {
      // todo
      // vuekey
      //  key: beforeMountmounted
      const nextVal = props[key];
      hostPatchProp(el, key, null, nextVal);
    }
  }

  // todo
  //  beforeMount() 
  console.log("vnodeHook  -> onVnodeBeforeMount");
  console.log("DirectiveHook  -> beforeMount");
  console.log("transition  -> beforeEnter");

  // 
  hostInsert(el, container);

  // todo
  //  mounted() 
  console.log("vnodeHook  -> onVnodeMounted");
  console.log("DirectiveHook  -> mounted");
  console.log("transition  -> enter");
}

function mountChildren(children, container) {
  children.forEach((VNodeChild) => {
    // todo
    //  vnodeChild
    //  vnode 
    console.log("mountChildren:", VNodeChild);
    patch(null, VNodeChild, container);
  });
}

function processComponent(n1, n2, container, parentComponent) {
  //  n1  mount
  if (!n1) {
    //  component
    mountComponent(n2, container, parentComponent);
  } else {
    // todo
    updateComponent(n1, n2, container);
  }
}

// 
function updateComponent(n1, n2, container) {
  // TODO
  console.log("", n1, n2);
}

function mountComponent(initialVNode, container, parentComponent) {
  // 1.  component instance
  const instance = (initialVNode.component = createComponentInstance(
    initialVNode,
    parentComponent
  ));
  console.log(`:${instance.type.name}`);
  // 2.  instance 
  setupComponent(instance);

  setupRenderEffect(instance, container);
}

function setupRenderEffect(instance, container) {
  //  render
  //  ctx  proxy
  // ctx  api
  //  renderComponentRoot 
  //  render

  // obj.name  = "111"
  // obj.name = "2222"
  // 
  //  ()
  //    effect 
  // 
  instance.update = effect(
    function componentEffect() {
      if (!instance.isMounted) {
        // 
        //  render 
        //  effect  render 
        // 
        console.log(" render, subTree");
        const proxyToUse = instance.proxy;
        //  render  this  proxy
        const subTree = (instance.subTree = instance.render.call(
          proxyToUse,
          proxyToUse
        ));
        console.log("subTree", subTree);

        // todo
        console.log(`${instance.type.name}: beforeMount hook`);
        console.log(`${instance.type.name}: onVnodeBeforeMount hook`);

        //  subTree  patch
        //  render  vnode 
        // 
        // 
        //  element 
        //  component
        // 
        //  subTree 
        // 
        //  subTree
        patch(null, subTree, container, instance);

        console.log(`${instance.type.name}: mounted hook`);
        instance.isMounted = true;
      } else {
        // 
        //  vnode  vnode 
        console.log("");
        //  subTree

        const proxyToUse = instance.proxy;
        const nextTree = instance.render.call(proxyToUse, proxyToUse);
        //  subTree
        const prevTree = instance.subTree;
        instance.subTree = nextTree;

        //  beforeUpdated hook
        console.log("beforeUpdated hook");
        console.log("onVnodeBeforeUpdate hook");

        //  vnode  vnode  patch 
        patch(prevTree, nextTree, prevTree.el, instance);

        //  updated hook
        console.log("updated hook");
        console.log("onVnodeUpdated hook");
      }
    },
    {
      scheduler: (effect) => {
        //  effect 
        queueJob(effect);
      },
    }
  );
}
